import './index.less';

module.exports = ['$scope', 'utils', 'landPageData', function($scope, utils, landPageData) {

	utils.addEvent('', '');

	$scope.data = landPageData;

	$scope.downLoadApp = () => {

		utils.addEvent('', '');
		
		utils.downLoadApp();
	}
}]


