
import './index.less';

module.exports = ['$scope', '$state', 'utils', 'question', function($scope, $state, utils, question) {

	utils.addEvent('', '');
	
	$scope.question = question;

	$scope.toContentDetail = () => {

		utils.addEvent('', '');

		$state.go('contentDetail');
	}

	$scope.reStudyContent = () => {

		utils.addEvent('', '');

		$state.go('studyType', {reSelect: true});
	}
}]


